import React, { useState, useEffect } from "react";
import "./App.css";
import axios from "axios";
import DataTable from "react-data-table-component";
import logo from "./img/logo.jpg";

const App = () => {
  const [data, setData] = useState();
  const columns = [
    {
      name: "ID",
      selector: "id",
      sortable: true,
    },
    {
      name: "TIME",
      selector: "time",
      sortable: true,
      right: true,
    },
    {
      name: "DATA",
      selector: "data",
      sortable: true,
      right: true,
    },
  ];

  useEffect(() => {
    axios.get("https://apiwalk.mqttcnx.com/getdata").then((res) => {
      setData(res.data);
    });
  }, []);

  return (
    <div className="app">
      <div className="title-web">
        <img src={logo} className="img-div" />
        <h1>WALKS FOR YOUR WAY</h1>
      </div>
      <br></br>
      <DataTable title="ข้อมูลการตรวจเจอ" columns={columns} data={data} />
    </div>
  );
};
export default App;
